/* Mixins */
/* Container */
:root {
  --container-border-radius: 4px;
}

.page {
  overflow-x: hidden;
}

.page-id-1654 .entry-content > * {
  margin: 0;
  padding: 0;
  max-width: unset;
  width: 100%;
}
.page-id-1654 section {
  width: 100%;
  max-width: unset !important;
}

div.textbox {
  opacity: 0.7;
  padding: 0.3em 0.5em;
  overflow-y: auto;
  max-height: 100%;
}

.textbox > span {
  padding: 0;
  opacity: 1;
  min-height: min-content;
}

