.vwui-btn:not(select).standard {
  background-color: var(--color-surface);
  color: var(--color-on-surface);
}
.vwui-btn:not(select).standard::before {
  box-shadow: inset 0 0 0 1px rgba(var(--color-on-surface-rgb), 0.3);
}
.vwui-btn:not(select).standard.error:not(.error.disabled) {
  color: var(--color-error);
  background-color: rgba(var(--color-error-rgb), 0.3);
}
.vwui-btn:not(select).standard.error:not(.error.disabled)::before {
  box-shadow: inset 0 0 0 1px rgba(var(--color-error-rgb), 0.5);
}
.vwui-btn:not(select).standard.active {
  color: rgba(var(--color-primary-rgb), 0.9);
}
.vwui-btn:not(select).standard.active:before {
  box-shadow: inset 0 0 0 1px rgba(var(--color-primary-rgb), 0.9);
}
.vwui-btn:not(select).primary {
  color: var(--color-on-primary);
  background-color: var(--color-primary);
  min-width: 80px;
  /* &:active {
  background-color: var(--color-primary);
  } */
  /* &.error:not(.error.disabled) {
      color: var(--color-on-error);
      background-color: var(--color-error);
      //box-shadow: 0 1px 1px 0 rgba(var(--color-error-rgb), 0.15), 0 3.4px 2.5px -1px rgba(var(--color-error-rgb), 0.1);

      &:hover {
