      }

      &:active {
      background-color: var(--color-error);
      }
  } */
}
.vwui-btn:not(select).primary:not(.error):hover {
  background-color: var(--color-primary);
}
.vwui-btn:not(select).secondary {
  background-color: rgba(var(--color-surface-variant-rgb), 0.6);
  color: rgba(var(--color-on-surface-rgb), 0.9);
}
.vwui-btn:not(select).secondary:hover {
  background-color: rgba(var(--color-surface-variant-rgb), 0.9);
