/* -------------------------------- 

Typography

-------------------------------- */
@font-face {
  font-family: "Roboto";
  src: url("Roboto-Regular.woff") format("woff");
  font-weight: 400;
}
@font-face {
  font-family: "Roboto";
  src: url("Roboto-Bold.woff") format("woff");
  font-weight: 700;
}
@font-face {
  font-family: "Roboto";
  src: url("Roboto-Medium.woff") format("woff");
  font-weight: 700;
}
@font-face {
  font-family: "Roboto";
  src: url("Roboto-Light.woff") format("woff");
  font-weight: 300;
}
@font-face {
  font-family: "MaterialIcons";
  src: url("MaterialIcons-Regular.woff") format("woff");
}
:root {
  --font-primary: "Roboto", sans-serif;
  --font-secondary: "Roboto", sans-serif;
  /* set base values */
  --text-base-size: 0.9em;
  --text-scale-ratio: 1.2;
  /* type scale */
  --text-xs: calc(1em / (var(--text-scale-ratio) * var(--text-scale-ratio)));
  --text-sm: calc(1em / var(--text-scale-ratio));
  --text-md: calc(1em * var(--text-scale-ratio));
  --text-lg: calc(1em * var(--text-scale-ratio) * var(--text-scale-ratio));
  --text-xl: calc(1em * var(--text-scale-ratio) * var(--text-scale-ratio) * var(--text-scale-ratio));
  --text-xxl: calc(1em * var(--text-scale-ratio) * var(--text-scale-ratio) * var(--text-scale-ratio) * var(--text-scale-ratio));
  --text-xxxl: calc(1em * var(--text-scale-ratio) * var(--text-scale-ratio) * var(--text-scale-ratio) * var(--text-scale-ratio) * var(--text-scale-ratio));
  /* line-height */
  --heading-line-height: 1.2;
  --body-line-height: 1.4;
}

/* @include breakpoint(md) {
    :root {
        --text-base-size: 1.25em;
        --text-scale-ratio: 1.25;
    }
} */
body, button, input, select, textarea {
  font-size: var(--text-base-size);
  font-family: var(--font-primary);
  color: var(--color-text);
}

h1, h2, h3, h4 {
  color: var(--color-text-heading);
  margin-bottom: var(--space-xxs);
  line-height: var(--heading-line-height);
}

/* text size */
.text--xxxl {
  font-size: var(--text-xxxl);
}

h1, .text--xxl {
  font-size: var(--text-xxl);
}

h2, .text--xl {
  font-size: var(--text-xl);
}

h3, .text--lg {
  font-size: var(--text-lg);
}

h4, .text--md {
  font-size: var(--text-md);
}

.text--sm, small {
  font-size: var(--text-sm);
}

.text--xs {
  font-size: var(--text-xs);
}

p {
  line-height: var(--body-line-height);
}

a {
  color: var(--color-link);
}
a:visited {
  color: var(--color-link-visited);
}

b, strong {
  font-weight: bold;
}

.text-container h2, .text-container h3, .text-container h4 {
  margin-top: var(--space-sm);
}
.text-container ul, .text-container ol, .text-container p {
  margin-bottom: var(--space-md);
}
.text-container ul, .text-container ol {
  list-style-position: outside;
  padding-left: 24px;
}
.text-container ul {
  list-style-type: disc;
}
.text-container ol {
  list-style-type: decimal;
}
.text-container ul li, .text-container ol li {
  line-height: var(--body-line-height);
}
.text-container em {
  font-style: italic;
}
.text-container u {
  text-decoration: underline;
}

/* utility classes */
.truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.text-danger {
  color: var(--color-accent);
}

