* {
  box-sizing: border-box;
}

section {
  padding: 0 !important;
}

html [data-color-scheme=light] {
  --popoverContainerBoxshadow: 0px 0px 0px 0.5px rgba(0, 0, 0, 0.15), 0px 2px 25px 0px rgba(0, 0, 0, 0.25);
  --accentBackgroundColor: #dedede;
  --color-text: #1c1c1c;
  --testBackground: #000;
}

.entry-content > :not(.alignwide):not(.alignfull):not(.alignleft):not(.alignright):not(.is-style-wide) {
  max-width: calc(100% - 4rem) !important;
}

.entry-content a:not(.material.icons):not(ul.control-group a):not(.vwui-tabs a) {
  color: var(--color-primary) !important;
  text-decoration: underline !important;
}

.baseline-box {
  display: flex;
  align-items: center;
  gap: 1em;
  width: 100%;
  flex-flow: row wrap;
}
.baseline-box.show-baseline p {
  position: relative;
}
.baseline-box.show-baseline p::after {
  width: 100vw;
  height: 1px;
  content: "";
  position: absolute;
  bottom: 0.35em;
  left: -280%;
  background-color: rgba(255, 0, 0, 0.23);
  pointer-events: none;
}

.palette-wrapper {
  height: 900px;
  min-width: 340px;
  margin-top: 2em;
  display: flex;
  resize: both;
  overflow: hidden;
  position: absolute;
  max-width: calc(100% - 4em) !important;
  max-height: calc(100vh - 6em) !important;
  left: 50%;
  top: 0;
  transform: translate(-50%, 0px);
  background: var(--dialog-background);
  border-radius: 1px;
  box-shadow: var(--popoverContainerBoxshadow);
  flex-flow: column;
}

.palette-title {
  display: flex;
  min-height: 20px;
  height: 19px;
  overflow: hidden;
  position: relative;
  margin: 0;
  padding: 0;
  background-color: var(--color-palette-title) !important;
}

span.palette-title {
  position: absolute;
  width: 100%;
  color: var(--color-on-surface);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0;
}

.title-edge {
  position: absolute;
  inset: 0 0 -2px 0;
  padding: 0 4px 1px 4px;
  box-shadow: var(--popoverContainerBoxshadow);
  background-color: var(--accentBackgroundColor);
  color: var(--color-text) !important;
  display: flex;
  gap: 4px;
  flex-flow: row nowrap;
  align-items: center;
  font-size: 76%;
  border-radius: 1px 1px 0 0;
}

.cs-right-area {
  color: var(--md-sys-color-on-surface);
  flex: 1;
  padding: 2em;
  min-height: 2000px;
}

.highlightrect {
  display: none;
}

/* Styleguide Pane */
.cs-sample-block {
  border-radius: 4px;
  position: relative;
  overflow: hidden;
}
.cs-sample-block:has(.vwui-menu) {
  overflow: visible;
}
.cs-sample-block::after {
  content: "";
  border-radius: 4px;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.2);
  width: 100%;
  position: absolute;
  inset: 0;
  pointer-events: none;
}

.cs-element-container {
  padding: 24px;
  background: var(--dialog-background);
  /* border-bottom: 1px solid var(--mdc-theme-on-surface, #EBECF0); */
  display: flex;
  align-items: center;
  gap: 1em;
}
.cs-element-container :has(+ div) {
  border-radius: 3px;
}

.cs-color {
  flex-flow: row nowrap;
  padding: 0;
  gap: 0;
  overflow: hidden;
}

/* Syntax Highlighting */
.wp_syntax {
  border: none;
  background: transparent !important;
}

div.code {
  background: transparent !important;
  padding: 1.8em;
  overflow-x: auto;
}
div.code:empty {
  display: none;
}

.code-highlight {
  background: rgba(0, 0, 0, 0.1);
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.5);
  border-radius: 2px;
  padding: 2px 4px;
  font-size: 90%;
}

.swatches {
  flex: auto;
  margin: 20px;
}

.swatch-title {
  padding: 12px;
  text-transform: capitalize;
  min-height: 65px;
  min-width: 170px;
  flex: 1 1 11.6%;
  font-weight: 400;
  flex-flow: column wrap;
  color: #fff;
}

.color-swatch:nth-child(n+7) {
