}

.color-swatch {
  padding: 12px;
  font-weight: bold;
  flex: 1 1 auto;
  min-height: 65px;
  flex-flow: column wrap;
  text-transform: capitalize;
  flex: 1 1 0;
}

.iframe-button {
  min-width: 750px !important;
  height: 750px !important;
  border: none;
  scale: 0.3333333;
  position: absolute;
  left: -100%;
  top: -100%;
  pointer-events: none;
}

.iframe-button-wrapper {
  width: 250px;
  height: 250px;
  overflow: hidden;
