/* -------------------------------- 

Tabs

-------------------------------- */
.vwui-tabs {
  color: var(--color-on-surface);
}
.vwui-tabs.vertical {
  list-style: none;
  display: flex;
  flex-flow: column;
  margin: 0;
}
.vwui-tabs.vertical hr {
  background: rgba(var(--color-on-surface-rgb), 0.2);
  height: 1px;
}
.vwui-tabs.vertical .vwui-tab {
  display: flex;
  align-items: center;
  flex: 0 1;
  gap: 8px;
  margin: 0 12px;
  padding: 6px 10px 6px;
  border-radius: 4px;
  cursor: pointer;
  position: relative;
}
.vwui-tabs.vertical .vwui-tab:hover {
  background-color: rgba(var(--color-surface-variant-rgb), 0.3);
}
.vwui-tabs.vertical .vwui-tab > img,
.vwui-tabs.vertical .vwui-tab svg {
  width: 24px;
  height: 24px;
}
.vwui-tabs.vertical .vwui-tab > span {
  white-space: nowrap;
}
.vwui-tabs.vertical .vwui-tab.active {
  background-color: rgba(var(--color-primary-rgb), 0.25);
  color: var(--color-primary);
