  } */
}

.vwui-tabs.horizontal {
  /* horizontal tabs */
  display: flex;
  justify-content: center;
  border-bottom: 1px solid var(--color-primary);
  width: 100%;
  gap: 22px;
}
.vwui-tabs.horizontal .vwui-tab {
  padding: 5px 14px;
  margin-bottom: 6px;
  border-radius: 16px;
  display: flex;
  flex-flow: column;
  align-items: center;
  background-color: transparent;
}
.vwui-tabs.horizontal .vwui-tab svg {
  width: 24px;
  height: 24px;
}
.vwui-tabs.horizontal .vwui-tab:hover {
  background-color: rgba(var(--color-surface-variant-rgb), 0.8);
}
.vwui-tabs.horizontal .vwui-tab.active {
  position: relative;
  color: var(--color-primary);
  pointer-events: none;
}
.vwui-tabs.horizontal .vwui-tab.active::after {
  content: "";
  width: calc(100% - 30px);
  height: 2px;
  border-radius: 2px 2px 0 0;
  background-color: var(--color-primary);
  position: absolute;
  bottom: -6px;
  left: 15px;
}

