/* -------------------------------- 

Grid & List Layouts, Cards

-------------------------------- */
.vwui-card {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  cursor: pointer;
  border-radius: 8px;
  padding: 8px;
  color: var(--color-on-background);
  position: relative;
  transition: ease 250ms;
}
.vwui-card:hover {
  background-color: rgba(var(--color-surface-variant-rgb), 0.3);
  transition: ease 300ms;
}
.vwui-card.active {
  background-color: rgba(var(--color-primary-rgb), 0.25);
  color: var(--color-primary);
}

.vwui-grid-container:not(:has(table)) {
  display: grid;
  gap: 2px;
  grid-template-columns: repeat(auto-fill, minmax(210px, 1fr));
}

.vwui-grid-container table {
  border: none;
}
.vwui-grid-container table td {
  border: none;
  padding: 4px 4px 0;
}
.vwui-grid-container table td svg {
  width: 60px;
}
.vwui-grid-container img {
  opacity: 0.9;
  border-radius: 2px;
  object-fit: cover;
  aspect-ratio: 1;
}
.vwui-grid-container span:empty {
  display: none;
}
.vwui-grid-container.list, .vwui-grid-container.card-w-full {
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
}
.vwui-grid-container.list .vwui-card-text-info, .vwui-grid-container.card-w-full .vwui-card-text-info {
  grid-template-columns: 1fr 2fr minmax(140px, 2fr);
  display: grid;
  width: 100%;
  gap: 4px;
  align-items: center;
}
.vwui-grid-container.list .vwui-card-text-info > div, .vwui-grid-container.card-w-full .vwui-card-text-info > div {
  display: flex;
  min-width: 100%;
}
.vwui-grid-container.list .vwui-card, .vwui-grid-container.card-w-full .vwui-card {
  flex-direction: row;
  gap: 8px;
}
.vwui-grid-container.list span:empty, .vwui-grid-container.card-w-full span:empty {
  display: inline-block;
}
.vwui-grid-container.list tbody, .vwui-grid-container.card-w-full tbody {
  display: table-row-group;
}
.vwui-grid-container.card-w-full {
  grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
}
.vwui-grid-container.card-w-full .vwui-card:not(.active):not(:has(+ .active)):not(:hover):not(:has(+ .vwui-card:hover)):after {
  content: "";
  width: 100%;
  height: 1px;
  display: block;
  border-bottom: 1px solid rgba(var(--color-on-surface-rgb), 0.1);
  position: absolute;
  bottom: -1px;
  left: 0;
}
.vwui-grid-container.card-w-full img,
.vwui-grid-container.card-w-full svg {
  max-width: 60px;
}
.vwui-grid-container tbody {
  display: grid;
  gap: 2px;
  grid-template-columns: repeat(auto-fill, minmax(210px, 1fr));
}
.vwui-grid-container.list {
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
}
.vwui-grid-container.list img {
  max-width: 80px;
}

#tableItemsList.list {
  grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
}
#tableItemsList.list img,
#tableItemsList.list svg {
  max-width: 60px;
}

